import React, { Component } from 'react';
import {Formik} from 'formik'
import * as Yup from 'yup'
import './SignUp.css';
import {Redirect} from 'react-router-dom';
import Cookies from 'universal-cookie';

class SignUp extends Component {
    constructor(props){
        super(props);
        this.state = {
            errorMessage: '',
            redirect: false,
            cookies: new Cookies()
        };
        this.signup = this.signup.bind(this);
    }

    // componentWillMount(){}
    // componentDidMount(){}
    // componentWillUnmount(){}

    // componentWillReceiveProps(){}
    // shouldComponentUpdate(){}
    // componentWillUpdate(){}
    // componentDidUpdate(){}
    signup(values, actions){
        fetch('https://cors-anywhere.herokuapp.com/https://bbook-backend.herokuapp.com/register/email-activation',{
            method: "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(
                {
                    name: values.fullname, 
                    password: values.password,
                    email: values.email,
                    phone: values.phoneNumber.toString(),
                }
            )
        })
        .then(res => {
            if(res.status === 400) {
                res.text().then(text => this.setState({errorMessage: text}));
                actions.setSubmitting(false);
            } else if (res.status === 200) {
                // this.props.LoginLogout(true);
                // this.setState({redirect: true});
                this.setState({
                    errorMessage:'Kim tra Email xc nhn!!!'
                    
                })
                actions.setSubmitting(false);
            } else {
                this.setState({errorMessage: 'Li khng xc nh'});
                actions.setSubmitting(false);
            }
        });
    }

    render() {
        if(this.state.redirect) {
            return (<Redirect to='/'/>)
        }

        if(this.state.cookies.get('isLogin')){
            return (<Redirect to='/'/>)
        }

        return (
            <div className="SignUp">
                <Formik
                    initialValues={{fullname: '', email: '', password: '',phoneNumber: ''}}
                    onSubmit={(values, actions) => {
                        this.signup(values, actions);
                    }}
                    validationSchema={Yup.object({
                        fullname: Yup.string()
                            .required('Tn ngi dng cn trng'),
                        email: Yup.string()
                            .email('Email khng hp l')
                            .required('Email khng c  trng'),
                        password: Yup.string()
                            .min(8, 'Qu ngn!!!')
                            .required('Mt khu khng c  trng'),
                        phoneNumber: Yup.number()
                            .moreThan(99999999, 'S in thoi t nht 9 ch s')
                            .positive('khng c c k t trong st'),
                    })}
                    >
                        {
                            props => (
                                <form onSubmit={props.handleSubmit} className="signup-form">
                                    <h1 style={{textAlign: "center"}} className="font-white">ng K</h1>
                                    <div className="form-item">
                                        <div className="form-item-header">
                                            <div className="font-white">Tn ng Nhp</div>
                                            {props.touched.email && props.errors.email? (
                                                <div className="invalid-message">{props.errors.fullname}</div>
                                            ) : null}
                                        </div>
                                        <input className="form-input"
                                            type="text"
                                            onChange={props.handleChange}
                                            value={props.values.fullname}
                                            name="fullname"
                                            placeholder="Hy nhp h v tn"
                                        />
                                    </div>
                                    <div className="form-item">
                                        <div className="form-item-header">
                                            <div className="font-white">Email</div>
                                            {props.touched.email && props.errors.email? (
                                                <div className="invalid-message">{props.errors.email}</div>
                                            ) : null}
                                        </div>
                                        <input className="form-input"
                                            type="email"
                                            onChange={props.handleChange}
                                            value={props.values.email}
                                            name="email"
                                            placeholder="Hy nhp email ca bn"
                                        />
                                    </div>
                                    <div className="form-item">
                                        <div className="form-item-header">
                                            <div className="font-white">Mt Khu</div>
                                            {props.touched.password && props.errors.password? (
                                                <div className="invalid-message">{props.errors.password}</div>
                                            ) : null}
                                        </div>
                                        <input className="form-input"
                                            type="password"
                                            onChange={props.handleChange}
                                            value={props.values.password}
                                            name="password"
                                            placeholder="Hy nhp mt khu ca bn"
                                        />
                                    </div>
                                    <div className="form-item">
                                        <div className="form-item-header">
                                            <div className="font-white">S in thoi</div>
                                            {props.touched.phoneNumber && props.errors.phoneNumber? (
                                                <div className="invalid-message">{props.errors.phoneNumber}</div>
                                            ) : null}
                                        </div>
                                        <input className="form-input"
                                            type="tel"
                                            onChange={props.handleChange}
                                            value={props.values.phoneNumber}
                                            name="phoneNumber"
                                            placeholder="0xxxxx"
                                        />
                                    </div>
                                    <div className="error-message">{this.state.errorMessage}</div>
                                    <input type="submit" disabled={props.isSubmitting} value="ng K"/>
                                </form>
                            )
                        }
                </Formik>
            </div>
        );
    }
}

export default SignUp;